import { theme } from '@utils/theme';
import React from 'react';
import styled, { ThemeProps } from 'styled-components';
import { Anchor } from './anchor';

export interface ILink {
  label: string;
  url: string;
  newTab: boolean;
}

export interface INavProps extends ThemeProps<typeof theme> {
  banner: React.ReactElement;
  bannerUrl: string;
  links: ILink[];
}

const NavWrapper = styled.ul<ThemeProps<typeof theme>>`
  list-style: none;
  margin: 0;
  padding: 15px;
  font-size: 24px;
  color: ${props => props.theme.colors.white};

  .banner-list {
    display: inline-block;
    margin-right: 32px;
  }
`;

const NavLink = styled.li`
  display: inline-block;
`;

export const Nav: React.FC<INavProps> = props => (
  <NavWrapper>
    <li className='banner-list'>
      <Anchor href={props.bannerUrl} highlight='#d0ffc1'>
        {props.banner}
      </Anchor>
    </li>
    {props.links.map(link => (
      <NavLink key={link.label}>
        <a href={link.url} {...(link.newTab ? { target: 'blank' } : {})}>
          {link.label}
        </a>
      </NavLink>
    ))}
  </NavWrapper>
);
